body {
  margin: 0;
  padding: 0;
  font-size: 36px;
}


body {

font-family: Verdana, Geneva, Tahoma, sans-serif;
}
button{
  border: 3px solid black;
  background: white;
  width: 200px;
  height: 80px;
  font-size: 20px;
  font-weight: bold;
  margin-top: 30px;
  cursor: pointer;
  margin-bottom: 100px;
}

button:hover{
  border: 1px solid black;
  background:black;
  cursor: pointer;
  color: white
}

label {
  display: block;
}
button{
  display: block;
}


.formReg, .formLog{

  width: 90%;
  border: 1px solid black;
  margin-left:auto;
  margin-right: auto;
  margin-bottom: 30px;
  padding: 20px;
  font-size: 60%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
